body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hide-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

:root {
  color-scheme: dark;
}

@font-face {
  font-family: 'California';
  src: local('California'), url(./fonts/CaliforniaPersonalUseRegular-L37ED.ttf) format('truetype');
}

@font-face {
  font-family: 'Gunfjaun';
  src: local('Gunfjaun'), url(./fonts/GunfjaunRunic-1lng.ttf) format('truetype');
}

@font-face {
  font-family: 'Remachine';
  src: local('Remachine'), url(./fonts/RemachineScriptPersonalUseOnly-yZL3.ttf) format('truetype');
}